@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;700&display=swap);
body{height:100vh;width:100vw;font-family:"Poppins",sans-serif;position:relative;background-image:linear-gradient(-225deg, #7408f0 0%, #5b54c2 100%)}figure{margin:0}.quiz__main{height:100vh;width:100vw}.quiz__box-container{width:300px;z-index:10000}.quiz__title{font-weight:700;font-size:1.5rem;color:#fff}.quiz__box{background-color:#fff;padding:20px;border-radius:10px}.quiz__answer{margin-top:30px;font-weight:600}.quiz__img{width:100%;position:relative}.quiz__img img{width:7rem;height:100px;position:absolute;top:-80px;left:170px}.quiz__question button{margin-bottom:15px;padding:5px;border:1px solid #c0bebea8;background-color:#fff;border-radius:5px;color:#585858;text-align:start}.quiz__question button:hover{color:#fff;background-color:#f9a826}.answer__correct{background-color:#60bf88 !important;color:#000 !important}.answer__incorrect{background-color:#ea8282 !important;color:#fff !important}.results__img,.results__img img{width:200px;height:200px}.results__title{font-weight:600;font-size:2rem}.results__points{font-size:1.5rem;font-weight:600;color:#60bf88}.btn-again{border:1px solid #c0bebea8;width:100%;font-weight:500}.btn-again:hover{background-color:#f9a826;color:#fff}
